import IconEmail from '@material-ui/icons/Email';
import IconWhatsApp from '@material-ui/icons/WhatsApp';
import "./Header.css";

const Header = () => {
    return (
        <header>
           
            <section className="topMenu">
                <div className="topMenu--area">
                    <div className="topMenu--info">
                        <div className="topMenu--email">
                            <IconEmail></IconEmail>
                            <span>contato@studyweb.com.br</span>
                        </div>
                        <div className="topMenu--whatsapp">
                            <IconWhatsApp></IconWhatsApp>
                            <span>(14) 4002-8922</span>
                        </div>
                    </div>

                    <a className="topMenu--contact" href="">Dúvidas? Entre em contato conosco.</a>
                </div>
            </section>

            <section className="menu--area">
                <nav>
                    <div className="menu--logo">
                        <span style={{color: '#535353'}}>Study</span>
                        <span style={{color: '#4DB7C6'}}>Web</span>
                    </div>

                    <ul className="menu--list">
                        <li><a href="">Início</a></li>
                        <li><a href="">Cursos</a></li>
                        <li><a href="">Carreira</a></li>
                        <li><a href="">Dúvidas</a></li>
                        <li className="menu--listSpecial"><a href="">Ensine na StudyWeb</a></li>
                    </ul>
                </nav>
            </section>

        </header>
    )
}

export default Header;