import "./Slide.css";

const Slide = () => {

    setTimeout(() => {
        const slide = document.querySelector('.slideHome--div');
        slide.style = "opacity:1; transition:0.7s; margin-left:12%";

    }, 300)

    return (
        <>
            <div className="slideHome--div">
                <h1 className="slideHome--title">Inicie a sua carreira de programador com a StudyWeb</h1>
                <p className="slideHome--text">Se você busca desenvolver novas habilidades na programação está no lugar certo. Oferecemos cursos gratuitos na área do desenvolvimento web e mobile para auxiliar na qualificação ou profissionalização do profissional de TI.</p>
                <div className="slideHome--link">
                    <a className="slideHome--linkLogin" href="">Entrar</a>
                    <a className="slideHome--linkCreate" href="">Cadastrar-se</a>
                </div>
            </div>
        </>
    )
}

export default Slide;