import "./CoursesCard.css";

const CoursesCard = (props) => {
    const cardTitle = props.title;
    const titleSize = props.titleSize;
    const btnMargin = props.btnMargin;
    const cardText = props.text;
    const cardImg = props.cardImg;

    document.addEventListener('scroll', () => {
        const scroll = window.scrollY;

        if(scroll >= 600){
            const courses = document.querySelectorAll('.coursesCard--card');
            courses.forEach(el => {
                el.style = "margin-top:0px; transition:0.5s";
            });

        } 
        
        if(scroll <= 300){
            const courses = document.querySelectorAll('.coursesCard--card');
            courses.forEach(el => {
                el.style = "margin-top:100px; transition:0.5s";
            });
            
        }
    });

    return (
        <>
            <div className="coursesCard--card">
               <img className="coursesCard--img" src={cardImg} alt="Imagem do curso"></img>
               <h1 style={titleSize} className="coursesCard--title">{cardTitle}</h1>
               <p className="coursesCard--text">{cardText}</p>
               <a style={btnMargin} className="coursesCard--link" href="">Saiba mais</a>
            </div>
        </>
    )
}

export default CoursesCard;