import "./WhyCreate.css";
import Item from './Item';
import IconGratuito from './icons/icon-gratuito.png'
import IconMetodologia from './icons/icon-metodologia.png'
import IconAtualizacao from './icons/icon-atualizacao.png'
import IconCertificado from './icons/icon-certificado.png'
import IconRelogio from './icons/icon-relogio.png'
import IconSuporte from './icons/icon-suporte.png'

const WhyCreate = () => {

    return (
        <>
           <h2 className="whyCreate--title">Porque estudar programação com a StudyWeb?</h2>

           <div className="whyCreate--icons">
                <Item 
                    icon={IconGratuito}
                    title="Cursos gratuitos" 
                    paragraph="Todos os cursos disponibilizados pela StudyWeb são gratuitos e de qualidade."
                />

                <Item 
                    icon={IconMetodologia}
                    title="Metodologia" 
                    paragraph="Os cursos disponibilizados e a metodologia de ensino são com foco no mercado de trabalho."
                />

                <Item 
                    icon={IconRelogio}
                    title="Flexibilidade" 
                    paragraph="As vídeo aulas sempre estarão disponíveis, dando flexibilidade para o aluno estudar quando quiser."
                />

                <Item 
                    icon={IconCertificado}
                    title="Certificado" 
                    paragraph="Ao termino dos cursos é disponibilizado um certificado que comprova a realiazação do mesmo."
                />

                <Item 
                    icon={IconAtualizacao}
                    title="Atualização" 
                    paragraph="Sempre que houverem novidades, os cursos serão atualizados."
                />

                <Item 
                    icon={IconSuporte}
                    title="Suporte" 
                    paragraph="Intermediamos o contato entre o professor e aluno para a solução de dúvidas."
                />
           </div>
        </>
    )
}

export default WhyCreate;