import "./TopCourses.css";
import CoursesCard from "./CoursesCard";

const TopCourses = () => {

    return (
        <>
           <div className="topCourses--intro">
               <h1 className="topCourses--title">Cursos mais bem-avaliados</h1>
               <p className="topCourses--text">Estes são os melhores cursos oferecidos pela <strong>StudyWeb</strong> de acordo com os nossos alunos.</p>
           </div>

           <div className="topCourses--courses">
                <CoursesCard 
                    title="Desenvolvimento Mobile com Flutter"
                    text="Aprenda a criar aplicativos multiplataformas com um dos melhores frameworks do momento."
                    cardImg="https://img.ibxk.com.br/2019/10/29/flutter-29101357380259.jpg"
                />
                <CoursesCard 
                    title="Desenvolvimento Web Completo"
                    titleSize={{fontSize: '1.5rem'}}
                    btnMargin={{bottom: '50px'}}
                    cardImg="https://idigitalcompany.com.br/images/blog/blog-15.png"
                    text="Aprenda HTML, CSS, Bootstrap, PHP, MySQL e muito mais neste curso. Ao final, você estará apto a desenvolver sites modernos." 
                />
                <CoursesCard 
                    title="Aprenda Banco de Dados com MySQL"
                    text="Aprenda a guardar informações inseridas pelos usuários de uma forma simples e segura."
                    cardImg="https://professor-falken.com/wp-content/uploads/2017/07/Como-obtener-resultados-aleatorios-en-consultas-de-MySQL-professor-falken.com_.jpg"
                />
           </div>

           <a className="topCourses--allCourses" href="">Ver todos os cursos</a>
        </>
    )
}

export default TopCourses;