import {Switch, Route} from 'react-router-dom';
import {BrowserRouter as Router} from 'react-router-dom';
import Home from '../../components/Home/Home';

const Content = () => {
    return (
        <>
            <Router>
                <Switch>
                    <Route path="/studyweb/" exact>
                        <Home />
                    </Route>
                </Switch>
            </Router>
        </>
    )
}

export default Content;